html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}


/* svg{
  width:100%;
  height:100%;
} */
#app{
  width:100%;
  height:100%;
}

#hitRing{
  cursor:pointer;
  -webkit-tap-highlight-color:transparent;
}

.disabled{
  pointer-events:none;
}

.enabled{
  pointer-events:auto;
}

.gradient-custom-2 {
  /* fallback for old browsers */
  background: #fccb90;
  
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593);
  
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593);
}
  
@media (min-width: 768px) {
  .gradient-form {
    height: 100vh !important;
  }
}
@media (min-width: 769px) {
  .gradient-custom-2 {
    border-top-right-radius: .3rem;
    border-bottom-right-radius: .3rem;
  }
}

.mb-0 {
  text-align: justify;
}

.mb-5 {
  margin: 0 auto;
}

.align {
  margin-top: 100px;
  text-align: center;
}

#header {
  width: 100%;
}

.project01, .transaction01 {
  display: flex;
  border: 1px solid #646464;
  max-width: 800px;
}

.p-name, .t-name {
  margin-top: 20px;
  margin-left: 20px;
  width: 50%;
  /* max-width: 400px; */
}

.p-grade, .p-result, .t-event, .t-amount {
  margin-top: 20px;
  margin-left: 20px;
  width: 12.5%;
  /* max-width: 100px; */
}

.p-update, .t-date {
  margin-top: 20px;
  margin-left: 20px;
  width: 25%;
  /* max-width: 150px; */
}

.bold {
  font-weight: bold;
}

.header {
  background-color: rgb(32, 95, 146);
  color: aliceblue;
}

.bar-chart {
  margin: 10px 0px;
  border: 1px solid #646464;
  background-color: aliceblue;
  max-width: 600px;
  overflow: visible;
  margin: 10px 10px;
}

.bar-chart text {
  font-size: 12px;
  fill: black;
}

.bar-chart rect {
  fill: rgb(32, 95, 146);
}

.bar-chart rect:hover {
  fill: rgb(105, 130, 150);
}

.liner-chart {
  margin: 10px 10px;
  border: 1px solid #646464;
  background-color: aliceblue;
  max-width: 600px;
  overflow: visible;
}

.liner-chart text {
  font-size: 12px;
  fill: black;
}

.liner-chart line {
  stroke: rgb(32, 95, 146);
  stroke-width: 2;
}

.liner-chart circle {
  fill: rgb(32, 95, 146);
  stroke: rgb(32, 95, 146);
  stroke-width: 2;
}

.liner-chart circle:hover {
  fill: rgb(105, 130, 150);
  stroke: rgb(105, 130, 150);
}

.liner-chart rect {
  fill: white;
  stroke: rgb(32, 95, 146);
  stroke-width: 2;
}
.display {
  display: block;
  justify-content: center;
  width: 600px;
  height: 500px;
  overflow: auto;
  border: 1px solid #646464;
  margin: 10px 10px;
}

.display div button {
  width: 25%;
}

@media only screen and (max-width: 600px) {
  #root {
    font-size: 10px;
  }
}